import React from 'react';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import { makeStyles } from '@material-ui/core/styles';
import {SearchTabTasksLabel, SearchTabTasksPanel} from './SearchTabTasks';
import {SearchTabFilesLabel, SearchTabFilesPanel} from './SearchTabFiles';
import {SearchTabCredentialsLabel, SearchTabCredentialsPanel} from './SearchTabCredentials';
import {SearchTabKeylogsLabel, SearchTabKeylogsPanel} from './SearchTabKeylog';
import {SearchTabTokensLabel, SearchTabTokensPanel} from './SearchTabTokens';
import {SearchTabCallbacksLabel, SearchTabCallbacksPanel} from './SearchTabCallbacks';
import {SearchTabArtifactsLabel, SearchTabArtifactsPanel} from './SearchTabArtifacts';
import {SearchTabSocksLabel, SearchTabSocksPanel} from './SearchTabSocks';

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    backgroundColor: theme.palette.background.paper,
  },
}));

export function Search(props){
  const classes = useStyles();
  const [value, setValue] = React.useState(0);
  const handleChange = (event, newValue) => {
      setValue(newValue);
  };
  const tabTypes = ["Callbacks", "Tasks", "Files", "Credentials", "Keylogs", "Tokens", "Artifacts", "Socks"]
return (
  <div className={classes.root} style={{height: "100%"}}>
      <AppBar position="static" color="default">
        <Tabs 
          value={value}
          onChange={handleChange}
          indicatorColor="primary"
          textColor="primary"
          variant="scrollable"
          scrollButtons="auto"
          aria-label="scrollable auto tabs example"
        >
        {
            tabTypes.map( (tab, index) => {
              switch (tab){
                case "Tasks":
                  return <SearchTabTasksLabel key={"taskstab"} />;
                case "Files":
                  return <SearchTabFilesLabel key={"filestab"} />;
                case "Credentials":
                  return <SearchTabCredentialsLabel key={"credentialstab"} />;
                case "Keylogs":
                  return <SearchTabKeylogsLabel key={"keylogstab"} />;
                case "Tokens":
                  return <SearchTabTokensLabel key={"tokenstab"} />;
                case "Callbacks":
                  return <SearchTabCallbacksLabel key={"callbackstab"} />;
                case "Artifacts":
                  return <SearchTabArtifactsLabel key={"artifactstab"} />;
                case "Socks":
                  return <SearchTabSocksLabel kye={"sockstab"} />;
                default:
                  return (null);
              }
            })
        }
        </Tabs>
      </AppBar>
      {
        tabTypes.map( (tab, index) => {
          switch(tab){
              case "Tasks":
                return <SearchTabTasksPanel key={"taskspanel"} value={value} index={index}/>
              case "Callbacks":
                return <SearchTabCallbacksPanel key={"callbackspanel"} value={value} index={index} />
              case "Files":
                return <SearchTabFilesPanel key={"filespanel"} value={value} index={index}/>
              case "Credentials":
                return <SearchTabCredentialsPanel key={"credentialspanel"} value={value} index={index} />
              case "Keylogs":
                return <SearchTabKeylogsPanel key={"keylogspanel"} value={value} index={index} />
              case "Tokens":
                return <SearchTabTokensPanel key={"tokenspanel"} value={value} index={index} />
              case "Artifacts":
                return <SearchTabArtifactsPanel key={"artifactspanel"} value={value} index={index} />
              case "Socks":
                return <SearchTabSocksPanel key={"sockspanel"} value={value} index={index} />
              default:
                return (null);
          }
        })
      }
  </div>
)
} 
