import React, { useEffect } from 'react';
import {Link, Typography} from '@material-ui/core';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { toLocalTime } from '../../utilities/Time';
import { meState } from '../../../cache';
import {useReactiveVar} from '@apollo/client';

export function KeylogsTable(props){
    const [keylogs, setKeylogs] = React.useState([]);
    useEffect( () => {
        setKeylogs([...props.keylogs]);
    }, [props.keylogs]);

    return (
        <TableContainer component={Paper} className="mythicElement" style={{height: "calc(78vh)"}}>
            <Table stickyHeader size="small" style={{"maxWidth": "100%", "overflow": "scroll"}}>
                <TableHead>
                    <TableRow>
                        <TableCell >User</TableCell>
                        <TableCell >Program</TableCell>
                        <TableCell>Timestamp</TableCell>
                        <TableCell >Host</TableCell>
                        <TableCell>Task</TableCell>
                        <TableCell >Keylog</TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                
                {keylogs.map( (op) => (
                    <KeylogTableRow
                        key={"cred" + op.id}
                        {...op}
                    />
                ))}
                </TableBody>
            </Table>
        </TableContainer>
    )
}

function KeylogTableRow(props){
    const me = useReactiveVar(meState);
    return (
        <React.Fragment>
            <TableRow hover>
                <TableCell>
                    <Typography variant="body2" style={{wordBreak: "break-all"}}>{props.user}</Typography>
                </TableCell>
                <TableCell >
                    <Typography variant="body2" style={{wordBreak: "break-all"}}>{props.window}</Typography>
                </TableCell>
                <TableCell>
                    <Typography variant="body2" style={{wordBreak: "break-all"}}>{toLocalTime(props.timestamp, me.user.view_utc_time)}</Typography>
                </TableCell>
                <TableCell >
                    <Typography variant="body2" style={{wordBreak: "break-all", display: "inline-block"}}>{props.task.callback.host}</Typography>
                </TableCell>
                <TableCell >
                    <Link style={{wordBreak: "break-all"}} color="textPrimary" underline="always" target="_blank" 
                        href={"/new/task/" + props.task.id}>
                            {props.task.id}
                    </Link>
                </TableCell>
                <TableCell>
                <Typography variant="body2" style={{wordBreak: "break-all", maxWidth: "40rem"}}>{props.keystrokes_text}</Typography>
                </TableCell>
            </TableRow>
        </React.Fragment>
    )
}

