import { IconButton } from '@material-ui/core';
import SendIcon from '@material-ui/icons/Send';
import React, {useEffect} from 'react';
import {TextField} from '@material-ui/core';
import Autocomplete from '@material-ui/lab/Autocomplete';
import TuneIcon from '@material-ui/icons/Tune';
import { MythicDialog } from '../../MythicComponents/MythicDialog';
import {CallbacksTabsTaskingFilterDialog} from './CallbacksTabsTaskingFilterDialog';
import {CallbacksTabsTaskingInputTokenSelect} from './CallbacksTabsTaskingInputTokenSelect';
import {matchSorter} from 'match-sorter';


export function CallbacksTabsTaskingInputPreMemo(props){
    const [message, setMessage] = React.useState("");
    const [highlighted, setHighlighted] = React.useState("");
    const [openAutocomplete, setOpenAutocomplete] = React.useState(false);
    const [autocompleteOptions, setAutocompleteOptions] = React.useState([]);
    const [loadedOptions, setLoadedOptions] = React.useState([]);
    const [taskOptions, setTaskOptions] = React.useState([]);
    const [openFilterOptionsDialog, setOpenFilterOptionsDialog] = React.useState(false);
    const [tokenOptions, setTokenOptions] = React.useState([]);
    useEffect( () => {
        const options = props.loadedOptions.reduce( (prev, cur)=>{
            if(prev.findIndex( e => e.cmd === cur.cmd) === -1){
                return [...prev, cur];
            }else{
                return [...prev];
            }
            
        }, [...props.nonLoadedCommands]);
        const cmds = options.map( c => c.cmd);
        setLoadedOptions(cmds);
        if(props.taskOptions){
            const prevCmds = props.taskOptions.task.map( (task) => {
                if(task.command){return task.command.cmd + " " + task.original_params;}
                else{return task.original_params};
            });
            const deDup = prevCmds.reduce( (prev, cur) => {
                if(prev.includes(cur)){return prev}
                return [...prev, cur]
            }, []);
            if(deDup.length !== taskOptions.length){
                setTaskOptions(deDup);
            }
            
        }else{
            if(taskOptions.length > 0){
                setTaskOptions([]);
            }
        }
    }, [props.loadedOptions, props.taskOptions, props.nonLoadedCommands]);
    const compareDicts = (left, right) => {
        if(Object.keys(left).length !== Object.keys(right).length){
            return false;
        }
        for(const [key, val] of Object.entries(left)){
            if(left[key] !== right[key]){
                return false;
            }
        }
        return true;
    }
    useEffect( () => {
        let updatedAny = false;
        const updated = props.tokens.reduce( (prev, cur) => {
            if(cur.callback.id !== props.callback_id){
                return [...prev];
            }
            let found = false;
            const updatedEntries = prev.map( (element) => {
                if(element.id === cur.id){
                    found = true;
                    updatedAny = compareDicts(cur, element);
                    return {...cur}
                }else{
                    return {...element}
                }
            });
            if(found){
                return [...updatedEntries]
            }else{
                updatedAny = true;
                return [...updatedEntries, cur];
            }
        }, [...tokenOptions]);
        if(updatedAny){
            setTokenOptions(updated);
        }
    }, [props.tokens])
    const handleInputChange = (evt, newInputValue) => {
        if(evt === null){
            //console.log("null inputChange", newInputValue, "message", message);
            return;
        }
        //console.log("inputChange", evt, evt.type, newInputValue);
        if(evt.type === "keydown" && evt.key === "Enter"){
            //console.log("seeing enter from input change, letting keydown handle it");
            return;
        }
        setMessage(newInputValue);
        if(newInputValue.length > 0 && !openAutocomplete && !newInputValue.includes(" ")){
            setAutocompleteOptions(loadedOptions);
            //console.log("opening autocomplete in handleInputChange with value", newInputValue);
            setOpenAutocomplete(true);
        }else if(newInputValue.length === 0){
            setOpenAutocomplete(false);
        }
    }
    const handleAutocompleteChange = (evt, value) => {
        //console.log("onChange", evt.type, evt.target.value, value, highlighted);
        if(evt.type === "change"){
            if(value.length > 0 && !value.includes(" ")){ 
                if(!openAutocomplete){
                    //console.log("handleAutocompleteChange opening autocomplete", value);
                    setOpenAutocomplete(true);
                    setAutocompleteOptions(loadedOptions);
                }else if(value.includes(" ")){
                    setOpenAutocomplete(false);
                }
            }else{
                setOpenAutocomplete(false);
                //console.log("handleAutocomplteChange setting new taskOptions");
                setAutocompleteOptions(taskOptions);
            }
        }else if(evt.type === "keydown"){
            if(evt.key === "Escape"){
                setOpenAutocomplete(false);
                return;
            }
            //console.log("handleAutocompleteChange", highlighted, value);
            if(highlighted !== null && evt.key === "Enter" && !value.includes(" ")){
                setMessage(highlighted + " ");
            }else{
                setMessage(value);
            } 
        }else if(evt.type === "click"){
            setMessage(value + " ");
            setOpenAutocomplete(false);
        }else if(evt.type === "blur"){
            evt.preventDefault();
            evt.stopPropagation();
            return;
        }
        if(evt.target.value.length === 0){
            setOpenAutocomplete(false);
            setAutocompleteOptions(loadedOptions);
        }else if(autocompleteOptions.length === 0){
            setOpenAutocomplete(false);
            setAutocompleteOptions(loadedOptions);
        }
    }
    const onKeyDown = (evt) => {
        //console.log("keydown_checker", evt.key);
        if(evt.code === "ArrowUp" || evt.code === "ArrowDown"){
            if(message.length === 0){
                //console.log(message, message.length, taskOptions);
                setAutocompleteOptions(taskOptions);
                setOpenAutocomplete(true);
            }
        }else if(evt.key === "Tab" ){
            if(highlighted !== "" && openAutocomplete){
                const highlight = highlighted;
                setMessage(highlight + " ");
                setHighlighted("");
                setOpenAutocomplete(false);
                evt.preventDefault();
                evt.stopPropagation();
            }
        }else if(evt.key === "Enter"){
            if(highlighted !== "" && openAutocomplete){
                const highlight = highlighted;
                setMessage(highlight + " ");
                setHighlighted("");
                setOpenAutocomplete(false);
                evt.preventDefault();
                evt.stopPropagation();
            }else if(!openAutocomplete){
                //console.log("submiting in onKeyDown");
                onSubmitCommandLine(evt);
            }
        }
    }
    const handleHighlightChange = (event, option, reason) => {
        //console.log("highlight change", event, option, reason);
        if(message.includes(" ")){
            //console.log("message has ' ' in handleHighlightChange");
            if(openAutocomplete){
                //console.log("autocomplete is open, closing it");
                setOpenAutocomplete(false);
            }
        }
        //console.log("handleHighlightChange", option, highlighted);
        if(reason !== "mouse" && option !== null){
            //console.log("highlight change", option, highlighted, message, message.includes(" "));
            if(option !== null){
                setHighlighted(option);
            }
        }
    }
    const onSubmitCommandLine = (evt) => {
        evt.preventDefault();
        evt.stopPropagation();
        setOpenAutocomplete(false);
        //console.log("onSubmitCommandLine", evt, message);
        props.onSubmitCommandLine(message);
        setMessage("");
        setHighlighted("");
    }
    const onClickFilter = () => {
        setOpenFilterOptionsDialog(true);
    }
    const filterOptions = (options, {inputValue}) => matchSorter(options, inputValue);
    
    return (
        <div style={{ position: "absolute", top:"92%", width: "100%"}}>
            <form onSubmit={onSubmitCommandLine}>
                <Autocomplete
                    freeSolo
                    autoHighlight={true}
                    open={openAutocomplete}
                    onOpen={(evt) => {
                        if(evt.type === "mousedown"){
                            setOpenAutocomplete(false);
                            return false;
                        }
                        if(!message.includes(" ")){
                            //console.log("opening autocomplete in onOpen with message", message);
                            setOpenAutocomplete(true);
                            return true;
                        }else{
                            setOpenAutocomplete(false);
                            return false;
                        }
                    }}
                    onClose={() => setOpenAutocomplete(false)}
                    id="free-solo-2-demo"
                    filterOptions={filterOptions}
                    disableClearable={true}
                    disableListWrap={true}
                    clearOnEscape={true}
                    inputValue={message}
                    size="small"
                    openOnFocus={false}
                    onHighlightChange={handleHighlightChange}
                    onChange={handleAutocompleteChange}
                    options={autocompleteOptions}
                    onInputChange={handleInputChange}
                    renderInput={(params) => {
                        return(
                          <TextField
                            {...params}
                            placeholder={"Start Typing a Command..."}
                            onKeyDown={onKeyDown}                         
                            size="small"
                            variant="outlined"
                            InputProps={{ ...params.InputProps, type: 'search',
                                endAdornment:
                                <React.Fragment>
                                <IconButton color="primary" variant="contained" onClick={onSubmitCommandLine}><SendIcon/></IconButton>
                                <IconButton color="secondary" variant="contained" onClick={onClickFilter}><TuneIcon/></IconButton>
                                </React.Fragment>,
                                startAdornment: <React.Fragment>
                                    {tokenOptions.length > 0 ? (
                                        <CallbacksTabsTaskingInputTokenSelect options={tokenOptions} changeSelectedToken={props.changeSelectedToken}/>
                                    ) : (null)}
                                    
                                </React.Fragment>
                           
                            }}
                          />
                        )
                    }}
                  />
              </form>
              {openFilterOptionsDialog &&
                <MythicDialog fullWidth={true} maxWidth="md" open={openFilterOptionsDialog} 
                    onClose={()=>{setOpenFilterOptionsDialog(false);}} 
                    innerDialog={<CallbacksTabsTaskingFilterDialog filterCommandOptions={loadedOptions} onSubmit={props.onSubmitFilter} filterOptions={props.filterOptions} onClose={()=>{setOpenFilterOptionsDialog(false);}} />}
                />
              }
              
        </div>  
    )
}
export const CallbacksTabsTaskingInput = React.memo(CallbacksTabsTaskingInputPreMemo);
